@keyframes zoom-in {
  0% {
    transform: scale(.9);
  }
  100% {
    opacity:1;
    transform: none;
  }
}

@keyframes zoom-in-from-left {
  0% {
    opacity:0;
    transform: translate(-25px);
  }
  100% {
    opacity:1;

  }
}
@keyframes zoom-in-from-right {
  0% {
    opacity:0;
    transform: translate(25px);
  }
  100% {
    opacity:1;

  }
}
