@import url('https://fonts.googleapis.com/css?family=Lato:300,400, 400i, 700, 700i, 900');
@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?b8lr5p');
  src:  url('../fonts/icomoon.eot?b8lr5p#iefix') format('embedded-opentype'),
  url('../fonts/icomoon.ttf?b8lr5p') format('truetype'),
  url('../fonts/icomoon.woff?b8lr5p') format('woff'),
  url('../fonts/icomoon.svg?b8lr5p#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

