//Video on slider - home
.js-video-popup {
  .video-inner {
    position: relative;
    margin: auto;
    width: 85vw;
    height: 50vh;
    @include medium {
      width: 85vw;
      height: 50vh;
    }
    @include large {
      width: 70vw;
      height: 70vh;
    }
  }
  display: none;
  &.visible {
    display: block;
  }
}

.js-video {
  width: 100%;
  height: 100%;
}

.btn-play {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  padding: 6px 0 10px;
  width: 200px;
  text-align: center;
  font-size: 25px;
  border: 2px solid $red;
  border-radius: 31px;
  background-color: rgb(17, 18, 21);
  box-shadow: 0 10px 10px -2px rgba(0, 0, 0, .5);
  color: $white;
  transition: all 0.3s;
  @include medium {
    width: 250px;
    font-size: 31px;
  }
  &:hover {
    color: $red;
    border-color: $black;
  }
}
//end of video in slider home

.btn-show-all {
  font-size: 18px;
  color: darken($white, 30%);
  padding: 6px 0;
  border: 2px solid currentColor;
  border-radius: 31px;
  box-shadow: 0 10px 10px -2px rgba(0, 0, 0, .5);
  transition: all 0.3s;
  text-align: center;
  display: block;
  width: 140px;
  @include small-down{
    margin: 0 auto;
  }
  &:hover {
    color: $white;
  }
}

.btn-marketing-kit {
  font-size: 18px;
  color: darken($white, 30%);
  padding: 6px 0;
  border: 2px solid currentColor;
  border-radius: 31px;
  box-shadow: 0 10px 10px -2px rgba(0, 0, 0, .5);
  transition: all 0.3s;
  text-align: center;
  display: block;
  width: 140px;
  margin-left: 20px;
  @include small-down{
    margin: 10px auto;
  }
  &:hover {
    color: $white;
  }
}

.js-close-video {
  position: absolute;
  top: -15px;
  right: -15px;
  z-index: 10;
  .icon {
    font-size: 30px;
    color: $red;
  }
}

