.usp {
  margin: 50px 0 0;
  @include medium {
    margin-top: 40px;
  }
  .items-wrap{
    display:flex;
    flex-wrap:wrap;
  }
  .item {
    font-size: 18px;
    padding: 0 30px;
    margin-bottom: 35px;
    text-align:center;
    @include medium {
      width:50%;
    }
    @include medium-only{
      padding: 0 15px;
      margin-bottom: 45px;
    }
    @include large{
      display: flex;
    }
  }
  .img-container {
    margin: 0 auto;
    margin-bottom: 20px;
    @include medium-only {
      height: 150px;
    }
    @include large {
      margin-bottom: 0;
    }
  }
  .img {
    width: 135px;
    max-width:none;
    @include medium{
      width: 100px;
    }
  }
  .text-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    text-align:center;
    @include large {
      justify-content: center;
      margin-left:25px;
      text-align:left;
    }
    .title {
      font-size: 23px;
      line-height: 1.25;
      font-weight: 700;
      margin: 0;
      margin-bottom:5px;
      color: #f53a3c;
      font-style: italic;
      text-transform: initial;
    }
    .desc {
      margin: 0;
      font-size:16px;
      line-height:1.4;
    }
  }
}

.section-news {
  padding-bottom: 50px;
  @include medium {
    padding-bottom: 100px;
  }
  .pagination {
    position: relative;
    border: 4px solid $red;
    border-radius: 25px;
    height: 47px;
    max-width: 208px;
    margin: 35px auto 0;
    text-align: center;
    @include medium {
      margin: 50px auto 0;
    }
    @include large {
      margin: 80px auto 0;
    }
    li {
      display: none;
      line-height: 38px;
      color: $red;
      font-size: 26px;
      &.slick-active {
        display: block;
      }
    }
    .slick-arrow {
      height: auto;
      text-indent: 0;
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      color: $red;
      vertical-align: middle;
      transition: all 0.4s;
      padding: 10px;
      &:focus {
        outline: none;
      }
      &.next-slide {
        right: 10px;
        &:hover {
          right: 5px;
        }
      }
      &.prev-slide {
        transform: rotate(-180deg);
        transform-origin: 50% 25%;
        left: 10px;
        &:hover {
          left: 5px;
        }
      }
    }
  }
}

