      background-color: $white;
    }
    &:hover {
      .js-article {
        bottom: 0;
        z-index: 10;
        height: 70%;
        .js-news-content {
          white-space: initial;
          overflow: visible;
          text-overflow: initial;
          .btn-play {
            opacity: 1;
            cursor: default;
            pointer-events: initial;
            z-index: 10;
          }
        }
      }
    }
  }
