    color: $red;
    button {
      color: $red;
      &:focus {
        outline: none;
      }
    }
  }
  li {
    display: none;
    &.slick-active {
      display: block;
    }
  }
}

.read-more {
  font-size: 16px;
  font-weight: 400;
  color: $red;
  transition: all 0.4s;
  &:after {
    content: "\e901";
    color: inherit;
    font-family: 'icomoon';
    font-size: 10px;
    margin-left: 5px;
    transition: margin-left 0.4s;
  }
  &:hover {
    &:after {
      margin-left: 7px;
    }
  }
}

.section-contact {
  padding-top: 30px;
  padding-bottom: 0;
  .row {
    display: flex;
    flex-wrap: wrap;
  }
  .columns {
    &:first-of-type {
      @include medium {
        padding-right: 25px;
        display: flex;
        flex-wrap: wrap;
      }
      @include large {
        padding-right: 55px;
      }
      .logo {
        max-width: 250px;
        align-self: flex-end;
        display: block;
      }
    }
    &:nth-of-type(2) {
      @include medium {
        padding-left: 25px;
      }
      @include large {
        padding-left: 55px;
      }
    }
  }
  .contact-form {
    font-size: 16px;
    @include small-down{
      margin-top:25px;
    }
    input,
    textarea {
      display: block;
      width: 100%;
      color: $black;
      padding: 5px 10px;
      &.invalid {
        border: 2px solid $red;
      }
    }
    textarea {
      resize: none;
      border-radius: 0;
    }
    input, textarea {
      border: none;
      &:focus {
        outline: none;
      }
    }
    input.invalid, textarea.invalid {
      border: 2px solid $red;
    }

    input.valid, textarea.valid {
      border: 2px solid $white;
    }

    .error {
      color: $red;
    }

    .ok {
      color: $white;
    }
    label {
      display: block;
      margin: 20px 0 5px;
      @include large {
        margin: 25px 0 5px;
      }
      &:first-of-type {
        margin-top: 5px;
      }
    }
    .g-recaptcha {
      margin-top: 15px;
    }
    .submit-btn {
      background: $red;
      color: $white;
      margin-top: 25px;
      padding: 10px 20px;
      border-radius: 3px;
      font-weight: 700;
      text-transform: uppercase;
      transition: all 0.3s;
      &:hover,
      &:focus {
        background: $white;
        color: $red;
      }
    }
  }
}

.text-container {
