/* Cookie warning */
.cookie-warn{
  position: fixed;
  bottom: 0;
  left:0;
  right:0;
  background: $black2;
  margin: 0;
  width: 100%;
  display: none;
  z-index: 10;
  padding: 12px 25px;
  font-size: 14px;
  &.close{
    bottom: -100%;
  }
  .cookie-inner{
    display:flex;
    align-items:center;
    p{
      margin:0;
      width:75%;
      @include medium{
        width:auto;
      }
    }
  }
  #close-cookie-warn{
    margin-left: auto;
    color: $white;
    background: $red;
    padding: 6px 10px;
    border-radius: 3px;
  }
}
