//VARIABLES
$header-height: 165px;
$header-height-small: 100px;

$black: #16171b;
$black2: #1e2127;
$white: #FFFFFF;
$red: #ff2748;
//Media Queries
$small-width: 320px;
$medium-width: 640px;
$large-width: 1024px;
$xlarge-width: 1440px;
$xxlarge-width: 1920px;

@mixin small-only {
  @media (min-width: #{$small-width}) and (max-width: #{$medium-width - 1px}) {
    @content;
  }
}
@mixin small-down {
  @media (min-width: #{$small-width}) and (max-width: #{$medium-width - 1px}) {
    @content;
  }
}
@mixin small {
  @media (min-width: #{$small-width}) {
    @content;
  }
}

@mixin medium-only {
  @media (min-width: #{$medium-width}) and (max-width: #{$large-width - 1px}) {
    @content;
  }
}
@mixin medium-down {
  @media (max-width: #{$large-width - 1px}) {
    @content;
  }
}
@mixin medium{
  @media (min-width: #{$medium-width}) {
    @content;
  }
}

@mixin large-only {
  @media (min-width: #{$large-width}) and (max-width: #{$xlarge-width - 1px}){
    @content;
  }
}
@mixin large-down {
  @media (max-width: #{$xlarge-width - 1px}){
    @content;
  }
}
@mixin large {
  @media (min-width: #{$large-width}) {
    @content;
  }
}

@mixin xlarge-only {
  @media (min-width: #{$xlarge-width}) and (max-width: #{$xxlarge-width - 1px}) {
    @content;
  }
}
@mixin xlarge-down {
  @media (max-width: #{$xxlarge-width - 1px}) {
    @content;
  }
}
@mixin xlarge {
  @media (min-width: #{$xlarge-width}) {
    @content;
  }
}

@mixin xxlarge {
  @media (min-width: #{$xxlarge-width}) {
    @content;
  }
}

@mixin retina {
  @media
  only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (min--moz-device-pixel-ratio: 2),
  only screen and (-o-min-device-pixel-ratio: 2/1),
  only screen and (min-device-pixel-ratio: 2),
  only screen and (min-resolution: 192dpi),
  only screen and (min-resolution: 2dppx) {
    @content;
  }
}

@mixin print {
  @media print {
    @content;
  }
}