.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: $header-height-small;
  background-color: transparentize($black, 0.05);
  display: flex;
  align-items: center;
  z-index: 9;
  transition: all 0.4s;
  @include medium {
    height: $header-height;
  }
  &.narrow {
    height: 60px;
    @include medium {
      height: 100px;
    }
    @include xlarge {
      height: 110px;
    }
    .logo-wrap {
      transition: all 0.4s;
      .logo-bg {
        transition: all 0.4s;
          width:100px;
        @include medium {
          width: 150px;
        }
        @include large {
          width: 180px;
        }
      }
    }
    .main-nav {
      @include medium-down {
        margin-top: 15px;
      }
      @include large {
        margin-bottom: 27px;
      }
      .menu {
        margin-bottom: 10px;
        .menu-item {
          font-size: 20px;
          @include xlarge {
            font-size: 23px;
          }
        }
      }
    }
    .social {
      @include large {
        margin-bottom: 20px;
      }
    }
  }
  .row,
  .columns {
    height: 100%;
  }
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    @include medium-only {
      flex-wrap: wrap;
      align-content: center;
    }
  }
  .social {
    margin-bottom: 7px;
    text-align: center;
    padding-left: 0;
    @include medium {
      text-align: right;
      animation: zoom-in-from-right ease-in-out 1;
      opacity: 0;
      animation-fill-mode: forwards;
      animation-duration: 1s;
      animation-delay: 0.8s;
    }
    @include medium-only {
      margin-top: 15px;
    }
    @include large {
      order: -1;
      text-align: left;
      animation-name: zoom-in-from-left;
    }
    li {
      width: 38px;
      height: 38px;
      margin-left: 15px;
      &:first-of-type {
        margin-left: 0;
      }
      a {
        .icon {
          font-size: 20px;
          color: $white;
          border: 2px solid $red;
          border-radius: 50%;
          padding: 6px;
          transition: all 0.3s;
          &.icon-linkedin {
            padding: 4px 6px 7px 6px;
          }
        }
        &:focus {
          outline: transparent;
        }
        &:hover {
          .icon {
            background: $red;
            color: $black;
          }
        }
      }
    }
  }
  .social,
  .main-nav {
    width: 100%;
    @include large {
      align-self: flex-end;
      width: 32%;
      margin-bottom: 40px;
    }
  }
  .logo-wrap {
    animation: zoom-in-from-left ease-in-out 1;
    opacity: 0;
    animation-fill-mode: forwards;
    animation-duration: 1s;
    animation-delay: 0.3s;
    -ms-animation-name: none;
    transition: all 0.4s;
    .logo-bg {
      transition: all 0.4s;
      width: 150px;
      @include medium {
        width: 220px;
      }
      @include large {
        width: 242px;
      }
    }
    @include medium {
      position: absolute;
      //width:220px;
      z-index: 10;
    }
    @include large {
      position: static;
      //width: 242px;
      animation-name: zoom-in;
    }
    a {
      position: relative;
      z-index: 10;
      display: block;
      height: 100%;
      transition: all 0.4s;
      &:focus,
      &:hover {
        outline: none;
      }
      &:hover {
        transform: scale(1.03);
      }
    }
  }
  .main-nav {
    @include medium-only {
      //margin-top: 30px;
    }
    .menu {
      margin-bottom: 15px;
      text-align: center;
      padding-left: 0;
      @include medium {
        text-align: right;
        animation: zoom-in-from-right ease-in-out 1;
        opacity: 0;
        animation-fill-mode: forwards;
        animation-duration: 1s;
        animation-delay: 0.8s;
      }
      .menu-item {
        position: relative;
        font-size: 25px;
        color: $white;
        margin-left: 40px;
        transition: all 0.4s;
        &.active {
          color: $red;
        }
        &:after {
          content: '';
          display: inline-block;
          padding-bottom: 10px;
          border-bottom: 2px solid $red;
          border-radius: 5px;
          position: absolute;
          left: 0;
          right: 0;
          bottom: -5px;
          transition: all 0.4s;
        }
        &:first-of-type {
          margin-left: 0;
        }
        &:hover {
          color: $red;
          &:after {
            bottom: -7px;
          }
        }
      }
    }
  }
  .js-menu-toggler {
    width: 40px;
    height: 35px;
    position: relative;
    margin-left: auto;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    cursor: pointer;
    animation: zoom-in-from-right ease-in-out 1;
    opacity: 0;
    animation-fill-mode: forwards;
    animation-duration: 1s;
    animation-delay: 0.3s;
    span {
      display: block;
      position: absolute;
      height: 6px;
      width: 50%;
      background: $red;
      opacity: 1;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: .25s ease-in-out;
      -moz-transition: .25s ease-in-out;
      -o-transition: .25s ease-in-out;
      transition: .25s ease-in-out;
      &:nth-child(even) {
        left: 50%;
        border-radius: 0 9px 9px 0;
      }
      &:nth-child(odd) {
        left: 0;
        border-radius: 9px 0 0 9px;
      }
      &:nth-child(1),
      &:nth-child(2) {
        top: 0;
      }
      &:nth-child(3),
      &:nth-child(4) {
        top: 13px;
      }
      &:nth-child(5),
      &:nth-child(6) {
        top: 26px;
      }
    }
    &.open {
      span {
        &:nth-child(1),
        &:nth-child(6) {
          -webkit-transform: rotate(45deg);
          -moz-transform: rotate(45deg);
          -o-transform: rotate(45deg);
          transform: rotate(45deg);
        }
        &:nth-child(2),
        &:nth-child(5) {
          -webkit-transform: rotate(-45deg);
          -moz-transform: rotate(-45deg);
          -o-transform: rotate(-45deg);
          transform: rotate(-45deg);
        }
        &:nth-child(1) {
          left: 4px;
          top: 9px;
        }

        &:nth-child(2) {
          left: calc(50% - 2px);
          top: 9px;
        }

        &:nth-child(3) {
          left: -50%;
          opacity: 0;
        }

        &:nth-child(4) {
          left: 100%;
          opacity: 0;
        }

        &:nth-child(5) {
          left: 4px;
          top: 23px;
        }

        &:nth-child(6) {
          left: calc(50% - 2px);
          top: 23px;
        }
      }
    }
  }
  .mobile-nav {
    display: none;
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    background-color: transparentize($black, 0.05);
    padding: 50px 0 30px;
    height: 100vh;
    z-index: -1;
    .menu {
      li {
        display: block;
        padding: 15px 0;
      }
    }
    .social {
      margin-top: 40px;
    }
  }
}

