html, body {
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  position:relative;
}
body {
  width:100%;
  height:100%;
  background-color: $black;
  font: 400 18px Lato;
  color: $white;
  line-height: 1.35;
  &.fixed{
    overflow-y: hidden;
    .js-video-popup {
      background: transparentize($black, 0.1);
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10;
    }
  }
  &.no-scroll{
    overflow-y: hidden;
  }
}

//Custom scrollbar
body::-webkit-scrollbar {
  width: 0.5em;
}
body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: $red;
}

a {
  text-decoration:none;
}
button{
  cursor: pointer;
}
a,
button {
  transition: all 0.3s;
  &:focus{
    outline:none;
  }
}
li {
  display: inline-block;
}
.row{
  &.expanded{
    width:100%;
    @include medium {
      width: 90%;
    }
    @include xlarge{
      width: 80%;
    }
    @include xxlarge{
      width: 70%;
    }
  }
}
.outer{
  display:table;
  width: 100%;
  height: 100%;
  .inner{
    display:table-cell;
    vertical-align: middle;
    text-align: center;
  }
}
.relative{
  position: relative;
}
.heading {
  font-size: 35px;
  font-weight: 900;
  color: $white;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 35px;
  @include medium {
    font-size: 55px;
  }
    @include medium-down {
      line-height: 2;
    }
    @include small-down {
      line-height: 2.2;
    }
  &__inner {
    border-top: 3px solid rgb(255, 39, 72);
    border-bottom: 3px solid rgb(255, 39, 72);
    padding: 5px 15px;
  }
  br{
    @include large{
      display: none;
    }
  }
}

