.banner {
  height: 300px;
  margin-top: $header-height-small;
  background: url(../images/careers-header.jpg);
  @include img-bg();
  @include medium {
    margin-top: $header-height;
    height: 450px;
  }
  .container {
    background: transparentize($black, .2);
    height: 100%;
    .row {
      height: 100%;
      .columns {
        height: 100%;
      }
    }
    .content {
      @include medium {
        font-size: 33px;
      }
    }
  }
}

.open-positions {
  color: $white;
  padding: 65px 0;
  h3 {
    font-size: 30px;
    text-transform: capitalize;
    font-weight: 400;
    text-align: left;
    margin-bottom: 35px;
    @include medium {
      font-size: 50px;
      margin-bottom: 65px;
    }
  }
  .category {
    margin-bottom: 25px;
    @include medium {
      margin-bottom: 40px;
    }
    &__title {
      font-size: 25px;
      border-bottom: 1px solid $red;
      padding-bottom: 3px;
    }
  }
  .careers-filter {
    .filters-wrap {
      color: $white;
      @include small-only {
        display: flex;
        flex-wrap: wrap;
        text-align: center;
      }
      a {
        font-size: 16px;
        display: block;
        position: relative;
        color: inherit;
        margin-bottom: 15px;
        &:focus {
          outline: transparent;
        }
        &:hover,
        &.active {
          color: $red;
        }
        @include medium {
          font-size: 20px;
        }
        @include small-only {
          width: 25%;
          position: relative;
          &.all {
            width: 100%;
            display: block;
          }
          &:before {
            content: '';
            display: inline-block;
            position: absolute;
            top: 50%;
            left: -2px;
            transform: translateY(-50%);
            width: 4px;
            height: 4px;
            border-radius: 25px;
            background: $red;
          }
          &:first-of-type,
          &:nth-of-type(2) {
            &:before {
              display: none;
            }
          }
        }
      }
    }
  }
  .text-container {
    margin-top: 80px;
  }
  .email {
    display: block;
    margin-top: 10px;
    @include medium {
      display: inline-block;
      padding-left: 5px;
    }
  }
  .careers-boxes {
    display: flex;
    flex-wrap: wrap;
    margin-top: 25px;
    @include medium {
      margin-top: 0;
    }
    .position-wrap {
      width: 100%;
      &:nth-of-type(odd) {
        background: #1e2127;
      }
      .position {
        position: relative;
        padding: 15px 20px;
        .columns {
          &:nth-of-type(2) {
            @include medium {
              padding-left: 0;
            }
          }
        }
        &-title,
        &-location {
          text-transform: capitalize;
          font-size: 18px;
        }
        .icon {
          position: absolute;
          top: 20px;
          right: 30px;
          width: 15px;
          height: 20px;
          text-align: right;
          transition: all 0.4s;
          color: $white;
          font-weight: 400;
          &.rotated {
            transform: rotate(90deg);
            transform-origin: 58% 50%;
            transition: all 0.4s;
          }
          @include large {
            position: static;
            margin-right: 15px;
          }
        }
      }
      .js-accordion-link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
      }
      .position-details {
        display: none;
        padding: 15px 20px  25px;
        ul{
          padding-left:0;
          li{
            font-weight: 300;
            margin-bottom:10px;
            display: block;
            &.empty{
              margin-bottom: 20px;
            }
          }
          &.bullet-list{
            padding-left:25px;
            li {
              display: list-item;
            }
          }
        }
        p{
          margin: 30px 0;
        }
        .email{
        padding-left:0;
          display:block;
        }
      }
    }
    .no-positions-popup {
      display: none;
      &.popup-visible {
        display: block;
      }
    }
  }
  .is-animated {
    @include medium {
      animation: .6s zoom-in;
    }
  }
}
