.section-games {
  margin-top: $header-height-small;
  height: auto;
  padding-top: 50px;
  @include medium {
    padding-top: 80px;
    margin-top: $header-height;
  }
  .slider-wrap {
    width: 80%;
  }
}

.slider-games {
  position: relative;
  padding: 30px 0;
  .item {
    position:relative;
    margin: 0 10px;
    @include img-bg();
    background-size: contain;
    height: 200px;
    //Slide-1
    &.item-1{
      background-image: url(https://games.eu.booming-games.com/v1/../metadata_files/5bd80c17b4d3b9000f00001b/logo.png);
    }
    //Slide-2
    &.item-2 {
      background-image: url(https://games.eu.booming-games.com/metadata_files/5bf661bf80797a00112ea73c/logo.png);
    }
    //Slide-3
    &.item-3 {
      background-image: url(https://games.eu.booming-games.com/v1/../metadata_files/5bc6f2806b226f000e0000ab/logo.png);
    }
    //Slide-4
    &.item-4 {
      background-image: url(https://games.eu.booming-games.com/v1/../metadata_files/5b8f8fc5408b77001600015d/logo.png);
    }
    //Slide-5
    &.item-5 {
      background-image: url(https://games.eu.booming-games.com/v1/../metadata_files/594791455f97bf001b00000d/logo.png);
    }
    //Slide-6
    &.item-6 {
      background-image: url(https://games.eu.booming-games.com/v1/../metadata_files/5bf6611580797a00112ea731/logo.png);
    }
  }
  .slick-arrow {
    width: 38px;
    height: 80px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.3s;
    cursor: pointer;
    &.prev-slide {
      left: -35px;
      @include medium {
        left: -53px;
      }
      &:hover {
        left: -45px;
        @include medium {
          left: -63px;
        }
      }
    }
    &.next-slide {
      right: -30px;
      @include medium {
        right: -50px;
      }
      &:hover {
        right: -40px;
        @include medium {
          right: -60px;
        }
      }
    }
  }
  .slick-list {
    overflow: hidden !important;
  }
}
//SEARCH
.search-wrap {
  background: $black2;
  margin-top: 20px;
  @include medium{
    margin-top: 60px;
  }
  &.fixed{
    position: fixed;
    top: 60px;
    left: 0;
    right:0;
    margin-top:0;
    z-index:3;
    @include medium{
      top: 100px;
    }
  }
  .search {
    margin: auto;
    height: 100%;
    position: relative;
    padding: 20px 0;
    display: flex;
    width: 80%;
    @include large {
      width: 60%;
    }
    .quicksearch {
      background: $black;
      border:none;
      width: 100%;
      height: 50px;
      text-align: right;
      color: $white;
      &:focus{
        outline:none;
      }
    }
    //remove default pseudoelement on input - IE
    .quicksearch::-ms-clear {
      display: none;
    }
    .btn-wrap{
      background: darken($black, 2%);
      width: 80px;
      position:relative;
    }
    .icon-search {
      font-size: 33px;
      color: $red;
      position:absolute;
      top:50%;
      left:50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
    }
    .btn-reset{
      display:flex;
      align-items:center;
      display:none;
      position:absolute;
      top:50%;
      left:50%;
      transform: translate(-50%, -50%);
      .icon{
        font-size: 25px;
      }
    }
  }
}
.btn-close-popup{
  display:none;
}
@media screen and (max-width: 920px) {
  .column-grid {
    width: 100%;
  }
  .column-options {
    position: fixed;
    top:0;
    left: -100%;
    bottom:0;
    background:transparentize($black, 0.1);
    width: 100%;
    height: 100%;
    transition: left 0.4s;
    &.visible{
      left:0;
      display:block;
      z-index: 99999999;
      padding-top: 60px;
      overflow:auto;
      transition: left 0.4s;
    }
    .btn-close-popup{
      display:block;
      position:absolute;
      top:30px;
      right:15px;
      z-index:9999999;
      .icon{
        font-size: 20px;
      }
    }
  }
  .search-wrap {
    .search {
      margin: 0;
      margin-left: auto;
    }
  }
  .js-mobile-filters{
    display:block;
    position: absolute;
    top: 50%;
    left:15px;
    transform: translateY(-50%);
    img{
      width: 30px;
      margin-bottom: -3px;
    }
  }
}
@media screen and (min-width: 921px) {
  .js-mobile-filters{
    display:none;
  }
}
@media screen and (min-width: 600px){
  .js-mobile-filters{
    left: 50px;
  }
}

.filters-games {
  width: 100%;
  visibility:hidden;
  &.loaded{
    visibility: visible;
  }
  .filter-link {
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    border: 2px solid $red;
    padding: 4px 10px 4px 10px;
    border-radius: 10px;
    font-weight: bold;
    text-transform: uppercase;
    width: 100%;
    color: $white;
    &.reset-filters {
      border: 2px solid $white;
    }
    .icon {
      font-size: 14px;
      line-height: 21px;
      color: $red;
      transition: all 0.4s;
    }
    &.open {
      .icon {
        transform: rotate(90deg);
        transform-origin: center center;
      }
    }
  }
  .options {
    margin-top: 30px;
    .option-wrap {
      margin-top: 20px;
      &:first-of-type {
        margin-top: 0;
      }
    }
  }
  .option-set {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 10px 0;
    .checkbox {
      margin-right: 15px;
    }
    &.features {
      display: block;
      .checkbox {
        display: block;
      }
    }
    &.themes {
      display: block;
      column-count: 2;
    }
    .checkbox {
      position: absolute;
      opacity: 0;
      // Box focus
      + label {
        font-size: 16px;
        margin-right: 10px;
      }
      &:hover + label {
        cursor: pointer;
        color: $red;
      }
      // Box checked
      &:checked + label {
        padding-bottom: 2px;
        border-bottom: 2px solid $red;
      }
    }
  }
  .game-box {
    position: relative;
    border-radius: 10px;
    margin: 5px;
    color: white;
    float: left;
    @include img-bg();
    //height: 380px;
    height: auto;
    max-width: 100%;
    width: 100%;
    @include medium {
      width: 47%;
    }
    @include xlarge {
      width: 30%;
    }
    img {
      width:100%;
      border-radius: 10px;
    }
    .game-details {
      font-size: 16px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
      background-color: transparentize($black2, 0.1);
      opacity:0;
      transition: opacity 0.4s;
      &-inner {
        padding: 15px;
      }
      span {
        font-size: inherit;
        color: #eee;
        text-shadow: 1px 1px 5px #20161e;
        &.label {
          font-weight: bold;
        }
      }
      &.visible {
        opacity:1;
      }
      .title {
        visibility: hidden;
        display: block;
      }
    }
    .game-link {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index:1;
    }
    .icon-plus {
      font-size: 14px;
      color: $red;
      position: absolute;
      bottom: 15px;
      right: 10px;
      background: $white;
      border-radius: 50%;
      padding: 10px;
      cursor: help;
      transition: all 0.3s;
      z-index:2;
      @include medium {
        font-size: 12px;
        padding: 8px;
      }
      &.rotate {
        transform: rotate(45deg);
      }
    }
  }
  #container {
    margin-top: 20px;
  }
}
