.slider-home {
  height: inherit;
  //@include small-down{
  //  height: 300px;
  //}
  //@include medium-only{
  //  height: 500px;
  //}
  //@include large {
  //  max-width: 1024px;
  //  margin: auto;
  //  height:calc(100vh - 160px);
  //}
  @media screen and (min-width: 1100px) {
    max-width: 1000px;
    margin: auto;
  }
  @media screen and (min-width: 1600px) {
    max-width: 1400px;
  }
  .slide {
    position: relative;
    height: inherit;
    @include img-bg();
    //background-position: 0 0;
    //&.slide-1 {
    //  background-image: url(../images/banner-home/Big_Apple_Wins_Banner.png);
    //}
    //&.slide-2 {
    //  background-image: url(../images/banner-home/ICE_Banner_2018.png);
    //  background-position: 0 0;
    //}
    //&.slide-3 {
    //  background-image: url(../images/banner-home/Romeo_Banner.png);
    //}
    //@include small-down{
     // background-position: 0 50%;
    //}
    //@include medium-only{
    //  background-position: 0 50%;
    //}
  }
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    overflow: hidden;
    @include img-bg();
    width: 30px;
    height: 50px;
    z-index: 1;
    text-indent: -9999999px;
    @include medium {
      width: 60px;
      height: 100px;
    }
    @include large{
      height: 150px;
    }
    &:focus {
      outline: none;
    }
    &.slick-prev {
      background-image: url(../images/icons/arrow-l.svg);
      left: 0;
      transition: all 0.4s;
      @include medium {
        left: 20px;
      }
      @include large {
        left: 50px;
      }

      &:hover {
        left: -4px;
        @include medium {
          left: 10px;
        }
        @include large {
          left: 35px;
        }
      }
    }
    &.slick-next {
      background-image: url(../images/icons/arrow-r.svg);
      right: 8px;
      transition: all 0.4s;
      @include medium {
        right: 23px;
      }
      @include large {
        right: 60px;
      }
      &:hover {
        right: 3px;
        @include medium {
          right: 13px;
        }
        @include large {
          right: 45px;
        }
      }
    }
  }
}

.slick-track,
.slick-list {
  height: inherit;
}

.play-button {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 33px;
  border: 2px solid rgb(242, 37, 68);
  border-radius: 31px;
  background-color: rgb(17, 18, 21);
  box-shadow: 0 10px 10px -2px rgba(0, 0, 0, .5);
  padding: 8px 40px 10px;
  @include small-down {
    display: none;
  }
}
.slick-dots {
  position: absolute;
  bottom: -75px;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 0;
  li {
    padding: 0 5px;
    button {
      text-indent: -9999999px;
      overflow: hidden;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background: #232930;
      box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.35), inset -1px -1px 3px rgba(0, 0, 0, 0.35);
      transition: all 0.3s;
      cursor: pointer;
      &:focus{
        outline:none;
      }
    }
    &:hover,
    &.slick-active {
      button {
        background: $red;
      }
    }
  }
}

.slick-disabled {
  opacity: 0.5;
  pointer-events: none;
}